import { useDispatch } from "react-redux";
import { setDroppedObjectData, setParentDetailsLoading } from "../store/droppedObjectSlice";
import { useState } from "react";

const useRevisions = () => {
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);

  const fetchRevisionsAndParents = (objectId, type, headers) => {
    console.log("[Fetch Revisions] Fetching revisions for object:", objectId);

    if (!headers) {
      console.error("[Fetch Revisions] Missing CSRF headers.");
      return;
    }

    const revisionURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/version/getGraph`;
    const parentURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/locate`;

    window.require(["DS/WAFData/WAFData"], (WAFData) => {
      WAFData.authenticatedRequest(revisionURL, {
        method: "POST",
        headers: {
          ...headers,
          "Content-Type": "application/json",
        },
        data: JSON.stringify({
          data: [
            {
              id: objectId,
              identifier: objectId,
              type: type,
              relativePath: `/resources/v1/modeler/dseng/dseng:EngItem/${objectId}`,
            },
          ],
        }),
        timeout: 150000,
        type: "json",
        onComplete: (revisionResponse) => {
          console.log("[Fetch Revisions] Revisions response:", revisionResponse);

          const versions = revisionResponse.results[0].versions;
          // Dispatch versions to Redux
          dispatch(
            setDroppedObjectData({
              versions,
            })
          );

          // Fetch parent information for each version
          const parentPromises = versions.map((revision) =>
            fetchParentInformation(revision, parentURL, WAFData, headers)
          );

          Promise.all(parentPromises)
            .then((parentDetails) => {
              const flattenedParents = parentDetails.flat();
              console.log("[Fetch Parents] Complete parent data:", flattenedParents);

              // Dispatch parent details to Redux
              dispatch(
                setDroppedObjectData({
                  parentDetails: flattenedParents,
                })
              );
              setLoading(false); // Clear loading state
            })
            .catch((error) => {
              console.error("[Fetch Parents] Error:", error);
              setLoading(false); // Clear loading state
            });
        },
        onFailure: (error) => {
          console.error("[Fetch Revisions] Failed:", error);
          setLoading(false); // Clear loading state
        },
      });
    });
  };


  const fetchParentInformation = (revision, parentURL, WAFData, headers) => {
    return new Promise((resolve) => {
      dispatch(setParentDetailsLoading(true)); // Start loading
      const requestBody = {
        referencedObjects: [
          {
            id: revision.id,
            identifier: revision.id,
            type: revision.type,
            relativePath: revision.relativePath,
            maturityState: revision.maturityState,
            revision: revision.revision,
            source: revision.source,
          },
        ],
      };

      console.log("[Fetch Parents] Payload:", requestBody);

      // Step 1: Fetch parent relationships
      WAFData.authenticatedRequest(parentURL, {
        method: "POST",
        headers: {
          ...headers, // Ensure CSRF headers are included
          "Content-Type": "application/json", // Explicitly set content type
        },
        data: JSON.stringify(requestBody),
        timeout: 150000,
        type: "json",
        onComplete: (parentResponse) => {
          console.log("[Fetch Parents] Parent response:", parentResponse);

          const parents =
            parentResponse.member?.[0]?.["dseng:EngInstance"]?.member || [];

          // Step 2: Fetch metadata for each parent
          const parentMetadataPromises = parents.map((parent) => {
            const parentObject = parent?.parentObject;
            if (!parentObject?.source || !parentObject?.relativePath) {
              console.error("[Fetch Parent Metadata] Invalid parentObject:", parentObject);
              return Promise.resolve(null); // Skip invalid parent objects
            }
            const metadataURL = `${parentObject.source}${parentObject.relativePath}?$mask=dsmveng:EngItemMask.Details`;

            console.log("[Fetch Parent Metadata] metadataURL:", metadataURL);
            return fetch(metadataURL, {
              method: "GET",
              headers: {
                ...headers,
                "Content-Type": "application/json",
                "x-requested-with": "xmlhttprequest", // Include the missing header
              },
              credentials: "include", // Include cookies in the request
            })
              .then((response) => {
                if (!response.ok) {
                  throw new Error(`HTTP error! Status: ${response.status}`);
                }
                return response.json();
              })
              .then((metadataResponse) => {
                const metadata = metadataResponse.member?.[0];
                return {
                  childId: revision.id,
                  connectedChildRevision: revision.revision,
                  parentId: parent.parentObject.identifier,
                  parentTitle: metadata?.title || "Unknown Title",
                  parentMetadata: metadata, // Attach detailed metadata for the parent
                };
              })
              .catch((error) => {
                console.error("[Fetch Parent Metadata] Error:", error);
                return null; // Resolve null on failure
              });
          });

          // Step 3: Resolve all metadata promises
          Promise.all(parentMetadataPromises).then((detailedParents) => {
            const validParents = detailedParents.filter((parent) => parent !== null);
            console.log("[Fetch Parents] Detailed parent data:", validParents);
            resolve(validParents);
            dispatch(setParentDetailsLoading(false)); // End loading
          });
        },
        onFailure: (error) => {
          console.error("[Fetch Parents] Failed for revision:", revision, error);
          resolve([]); // Resolve empty data on failure
          dispatch(setParentDetailsLoading(false)); // End loading on failure
        },
      });
    });
  };

  return { fetchRevisionsAndParents, loading };
};

export default useRevisions;
